<template>
  <div>
    <div class="cursor"></div>
    <div class="cursor-f">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="50"
        height="50"
        viewBox="0 0 256 256"
      >
        <path
          fill="#000000"
          d="M214.58 41.42C196.86 23.71 171 16.63 141.81 21.5c-27.74 4.62-55.46 19.62-78.08 42.23s-37.61 50.34-42.23 78.08c-4.87 29.21 2.21 55.05 19.92 72.77C55.48 228.64 74.67 236 96.6 236a107.4 107.4 0 0 0 17.59-1.5c27.74-4.62 55.46-19.62 78.08-42.23s37.61-50.34 42.23-78.08c4.87-29.19-2.21-55.05-19.92-72.77M45.17 145.75c3.8-22.84 16.42-45.94 35.53-65S122.91 49 145.75 45.17A82 82 0 0 1 159.27 44a62.8 62.8 0 0 1 18.24 2.57a110 110 0 0 0-22.89 13.83c-20 15.77-32.88 37.72-38.38 65.25c-8.95 44.73-40.3 61.1-62.07 67.09c-8.92-11.91-12.11-28.33-9-46.99m165.66-35.5c-3.8 22.84-16.42 45.94-35.53 65.05s-42.21 31.7-65.05 35.53c-11.65 1.94-22.4 1.41-31.75-1.42a109.4 109.4 0 0 0 22.87-13.81c20-15.77 32.89-37.72 38.39-65.25c9-44.73 40.3-61.1 62.07-67.09c8.92 11.91 12.11 28.33 9 46.99"
        />
      </svg>
    </div>
  </div>
</template>


<script>
import gsap from "gsap";
export default {
  methods: {},
  mounted() {
    const cursor = document.querySelector(".cursor");
    const cursorF = document.querySelector(".cursor-f");
    let cursorX = 0;
    let cursorY = 0;
    let pageX = 0;
    let pageY = 0;
    let size = 8;
    let sizeF = 36;
    let followSpeed = 0.16;
    if ("ontouchstart" in window) {
      cursor.style.display = "none";
      cursorF.style.display = "none";
      return;
    }
    function lerp(start, end, amount) {
      return (1 - amount) * start + amount * end;
    }
    cursor.style.setProperty("--size", size + "px");
    cursorF.style.setProperty("--size", sizeF + "px");
    this.mymousemove = (e) => {
      pageX = e.clientX;
      pageY = e.clientY;
      cursor.style.left = e.clientX - size / 2 + "px";
      cursor.style.top = e.clientY - size / 2 + "px";
    };
    window.addEventListener("mousemove", this.mymousemove);

    document.body.appendChild(cursor);
    document.body.appendChild(cursorF);
    loop();

    let startY;
    let endY;
    let clicked = false;
    function loop() {
      cursorX = lerp(cursorX, pageX, followSpeed);
      cursorY = lerp(cursorY, pageY, followSpeed);
      cursorF.style.top = cursorY - sizeF / 2 + "px";
      cursorF.style.left = cursorX - sizeF / 2 + "px";
      requestAnimationFrame(loop);
    }
    this.mymousedown = (e) => {
      gsap.to(cursor, { scale: 4.5 });
      gsap.to(cursorF, { scale: 0.4 });
    };
    this.mymouseup = (e) => {
      gsap.to(cursor, { scale: 1 });
      gsap.to(cursorF, { scale: 1 });
    };

    window.addEventListener("mousedown", this.mymousedown, false);
    window.addEventListener("mouseup", this.mymouseup, false);
  },
  beforeDestroy() {
    window.removeEventListener("mousemove", this.mymousemove);
    window.removeEventListener("mousedown", this.mymousedown);
    window.removeEventListener("mouseup", this.mymouseup);
  },
};
</script>


<style scoped>
