<template>
    <div
      ref="animatedElement"
      :class="{ 'animate__animated': isVisible, [animationClass]: isVisible }"
      :style="initialStyle"
    >
      <slot></slot>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      animationClass: {
        type: String,
        default: 'animate__bounceInUp'
      },
      initialStyle: {
        type: Object,
        default: () => ({
          opacity: 0, // 设置元素初始样式
        })
      },
      once: {
        type: Boolean,
        default: true // 设为 true 则动画只触发一次
      }
    },
    data() {
      return {
        isVisible: false,
      };
    },
    mounted() {
      const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            this.isVisible = true;
            if (this.once) {
              observer.unobserve(entry.target); // 动画触发后停止监听
            }
          } else if (!this.once) {
            this.isVisible = false; // 如果不是一次性动画，离开视口时移除动画
          }
        });
      });
  
      observer.observe(this.$refs.animatedElement);
    },
  };
  </script>
  
  <style scoped>
  /* 如果需要，可以在这里添加默认样式 */
  </style>
  