<template>
  <div
    ref="gsapbox"
    class="relative flex min-h-screen m-auto overflow-hidden horizontal-scroll rounded-2xl"
  >
    <slot></slot>
  </div>
</template>

<script>
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default {
  methods: {
    initgsapbox() {
      if (this.scrollTriggerInstance) {
        this.scrollTriggerInstance.scrollTrigger.kill(); // 清除之前的实例
      }

      const imgbox = this.$refs.gsapbox.querySelector("img");
      if (!imgbox) {
        console.error("No image found in gsapbox.");
        return;
      }

      this.scrollTriggerInstance = gsap.to(imgbox, {
        scrollTrigger: {
          trigger: this.$refs.gsapbox,
          start: "top center",
          end: "bottom center",
          scrub: 1,
          markers: false,
          repeatRefresh: true,
        },
        y: "70%",
      });

      // 触发 ScrollTrigger 刷新
      ScrollTrigger.refresh();
    },
  },

  mounted() {
    const imgbox = this.$refs.gsapbox.querySelector("img");
    if (imgbox.complete) {
      this.initgsapbox();
      ScrollTrigger.refresh();
    } else {
      imgbox.onload = () => {
        this.initgsapbox();
        ScrollTrigger.refresh();
      };
    }
  },

  beforeDestroy() {
    // 在组件销毁时清除ScrollTrigger实例
    if (this.scrollTriggerInstance) {
      this.scrollTriggerInstance.scrollTrigger.kill();
    }
  },
};
</script>

<style scoped>
.horizontal-scroll {
  overflow-x: auto; /* 启用横向滚动 */
  white-space: nowrap; /* 防止内容折行 */
  position: relative; /* 确保相对定位 */
  width: 100%; /* 确保宽度足够 */
}
</style>
