</template>

<script>
import Lenis from "lenis";
import MouseMove from "./components/MouseMove.vue";

export default {
  components: { MouseMove },
  name: "App",
  mounted() {
    const lenis = new Lenis();
    this.$root.$lenis = lenis;
    let lenisEnabled = true;
    function raf(time) {
      lenis.raf(time);
      requestAnimationFrame(raf);
    }
    requestAnimationFrame(raf);
  },
};
</script>




