<template>
  <div class="w-full min-h-screen">
    <div class="w-full min-h-screen">
      <project-header></project-header>
      <div class="flex w-full min-h-screen p-10">
        <div class="w-full px-3 mt-5 content">
          <h1 id="pxTitle">
            <span>PX传感器的构建</span>
          </h1>
          <h2 id="pxDetails"><span>质粒构建</span></h2>
          <h3>
            首先我们计划构建pYB1s-ndmDCEA质粒，转入BW-△guaB中，这个可以对NdmB的咖啡因降解能力进行对比，选择在96孔板里面进行培养。
            其中pYB1s-ndmB能将咖啡因降解成副黄嘌呤
            pYB1s-ndmDCEA能将副黄嘌呤降解成黄嘌呤
          </h3>
          <img src="@/assets/images/PX传感器的构建/1.png" alt="" />
          <h3>
            利用pYB1s-ndmB为生产菌株，pYB1s-ndmDCEA为测试菌株
            若咖啡因降解能力强，产生的PX会被转化为黄嘌呤，大肠杆菌存活
            若咖啡因降解能力差，大量的咖啡因会被转化为3-MX不会使大肠杆菌存活
            在筛选过程中，ndmB基因编码的酶扮演着关键角色。如果ndmB酶活性强，它能更有效地催化咖啡因降解的初始步骤，生成副黄嘌呤（PX），随后PX被进一步转化为黄嘌呤。然而，在黄嘌呤缺陷型的大肠杆菌中，由于无法合成黄嘌呤，但是细胞对黄嘌呤仍有依赖，其存活能力仍与ndmB能否有效降解咖啡因密切相关。通过这个机理我们可以使用黄嘌呤缺陷型的大肠杆菌去筛选出ndmB突变后酶活性增强的突变体
          </h3>
          <img src="@/assets/images/PX传感器的构建/2.png" alt="" />
          <h3>
            先构建pYB1s-ndmDCEA质粒，该工作流程从获取ndmDCE片段和ndmA片段开始，使用pcr获取所需片段，然后在
            1% 琼脂糖凝胶上验证产物，通过切胶回收获得对应片段
          </h3>
          <img src="@/assets/images/PX传感器的构建/3.png" alt="" />
          <img src="@/assets/images/PX传感器的构建/4.png" alt="" />
          <h3>
            获取对应片段之后，我们使用Gibson连接片段和载体，化转，涂抗性平板，之后选择单克隆菌落进行菌落PCR验证，阳性率在78%
          </h3>
          <img src="@/assets/images/PX传感器的构建/5.png" alt="" />
          <h3>
            进行对pYB1s-ndmDCEA的功能验证
            我们将测序正确的pYB1s-ndmDCEA质粒转入BW△guaB，挑单克隆做诱导，诱导18h
          </h3>
          <img src="@/assets/images/PX传感器的构建/6.png" alt="" />
          <h3>
            之后水洗ZY中的黄嘌呤，离心后弃上清，用M9培养基洗一次后离心，取5ml
            M9重悬作为菌液。
            然后在M9试管中培养，分别加入终浓度为0.5mM的TP、TB、PX、Caf各两管，以及两管对照。
          </h3>
          <img src="@/assets/images/PX传感器的构建/7.png" alt="" />
          <h3>
            在进行完功能验证后，我们对pYB1s-ndmDCEA进行测试，首先我们在加入不同的底物的情况下，对敲除黄嘌呤合成基因的BW25113菌株进行测试，分别是添加Caf，TP，Tb，Xan，PX，None，然后观察其36h的生长曲线。
          </h3>
          <img src="@/assets/images/PX传感器的构建/8.png" alt="" />
          <h3>
            在测试完不同底物对BW-△guaB的生长影响之后，我们选择加入不同浓度副黄嘌呤观察对BW-△guaB的影响，分别选择0mMPX，0.1mMPX，0.2mMPX，0.3mMPX。然后观察其36h的生长曲线。
            并且DCEA在0~0.3mM的PX溶液里体现较好的梯度，可以用于检测是否有PX产生。
          </h3>
          <img src="@/assets/images/PX传感器的构建/9.png" alt="" />
          <h3>
            之后我们在进行测试，先将DCEA诱导，其中我们在诱导时候加了一点黄嘌呤粉末，诱导完成后水洗菌体，用M9重悬，10%接种到含甲基黄嘌呤的M9中，但是都需要培养很长时间（36h）才能看出区别，并且菌都是先生长之后才开始受抑制或死亡（可能和诱导时加的黄嘌呤没有完全溶解有关）但不加额外的黄嘌呤会导致菌生长过于缓慢，LB中黄嘌呤主要来自酵母提取物，所以我们加入不同量的酵母提取物，然后在96孔板中绘制生长曲线，该结果可以证明LB中的黄嘌呤含量是不足的，所以我们根据此结果选择将酵母提取物翻一番即可较好的补充，之后使用“LB
            Y2”对菌株进行扩增和诱导。
          </h3>
          <img src="@/assets/images/PX传感器的构建/10.png" alt="" />
          <h1 id="ndmbTitle"><span>对NdmB进行突变，并选择产量较高的菌株</span></h1>
          <h3>
            1.NdmB的Vanillate O-demethylase oxygenase C-domain进行全局突变
            2.NdmB可能的口袋位点进行定点突变
            使用简并引物对质粒PCR从而突变Trp256，Cys267和Met271，之前已经随机突变过得到ndmBs，与载体进行单片段Gibson连接，使用简并引物对质粒PCR从而突变Gln289和Leu293
            使用咖啡因生产副黄嘌呤 pYB1s-ndmB能将咖啡因降解成副黄嘌呤
            pYB1s-ndmDCEA能将副黄嘌呤降解成黄嘌呤
            所以我们利用pYB1s-ndmB为生产菌株，pYB1s-ndmDCEA为测试菌株
            第一轮测试我们在酶标仪下检测OD值，生长良好的为可以生产PX的菌株
          </h3>
          <img
            src="@/assets/images/对NdmB进行突变，并选择产量较高的菌株/1.png"
            alt=""
          />
          <img
            src="@/assets/images/对NdmB进行突变，并选择产量较高的菌株/2.png"
            alt=""
          />

          <h3>
            之后我们选OD值较高的菌进行HPLC检测，将有产量的六个菌株提质粒，测序
          </h3>
          <img
            src="@/assets/images/对NdmB进行突变，并选择产量较高的菌株/3.png"
            alt=""
          />
          <img
            src="@/assets/images/对NdmB进行突变，并选择产量较高的菌株/4.png"
            alt=""
          />
          <h3>
            接下来我们进行了pY38a-DpnI的构建，测试测序正确 转入BW验证其功能
            我们进行了一轮测试，我们选择将F11与质粒frmAB同时转入BW菌株当中，以提高转化效率
            选择在25摄氏度诱导18h，进行全细胞催化20℃放置18h，进行制样然后HPLC检测产量，添加辅因子再生系统（CRS）可以使得产量有一定增加，但仍然低于报道的最高产量
          </h3>
          <img
            src="@/assets/images/对NdmB进行突变，并选择产量较高的菌株/5.png"
            alt=""
          />
        </div>
        <div class="z-50 ml-auto max-w-72 min-w-40" ref="tocFar">
          <div
            class="sticky w-full max-h-screen p-2 ml-auto shadow-inner toc rounded-xl top-[60px]"
            ref="tocBox"
          ></div>
        </div>
      </div>
    </div>
    <my-header></my-header>
    <my-footer></my-footer>
  </div>
</template>

<script>
import ProjectHeader from "./components/ProjectHeader.vue";
import tocbot from "tocbot";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export default {
  components: {
    ProjectHeader,
  },
  methods: {
    initTocbot() {
      tocbot.init({
        tocSelector: ".toc", // 放置目录的容器
        contentSelector: ".content", // 包裹内容的容器
        headingSelector: "h1, h2", // 包括在目录中的标题级别
        collapseDepth: 2, // 可折叠的深度
        scrollSmooth: true, // 平滑滚动
      });
    },
    initToc() {
      this.tocbox = gsap.to(this.$refs.tocBox, {
        scrollTrigger: {
          trigger: this.$refs.tocFar,
          start: "top top",
          end: "bottom bottom",
          // scrub: 1,
          markers: true,
          repeatRefresh: true,
        },
        y: () => {
          const tocHeight = this.$refs.tocBox.offsetHeight;
          const containerHeight = this.$refs.tocFar.offsetHeight;
          const scrollDistance = containerHeight - tocHeight;
          return scrollDistance > 0 ? scrollDistance : 0;
        },
      });
    },
  },
  mounted() {
    this.initTocbot();
    // this.initToc();
  },
};
</script>

