<template>
  <div
    class="fixed top-0 w-full"
    ref="headnav"
    style="transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1); z-index: 999"
  >
    <div
      class="flex w-1/2 px-2 py-1 mx-auto mt-5 bg-white h-14 rounded-xl max-lg:w-2/3"
    >
      <img
        src="https://static.igem.wiki/teams/4765/wiki/czy/logo-czy.ico"
        alt=""
        class="z-20 h-full"
      />
      <div class="z-20 flex h-full ml-2" style="mix-blend-mode: difference">
        <div class="m-auto text-xl font-bold text-white select-none">
          <router-link to="/">Nefuuu</router-link>
        </div>
      </div>
      <div
        class="flex justify-around w-full h-full max-lg:hidden justify-items-center"
      >
        <ul class="flex h-full m-auto">
          <li class="py-3 mr-5 duration-100 cursor-pointer hover:text-blue-700">
            Team
          </li>
          <li class="py-3 mr-5 duration-100 cursor-pointer hover:text-blue-700">
            <router-link to="/project">Project</router-link>
          </li>
          <li class="py-3 mr-5 duration-100 cursor-pointer hover:text-blue-700">
            Parts
          </li>
          <li class="py-3 duration-100 cursor-pointer hover:text-blue-700">
            Human Practices
          </li>
        </ul>
      </div>
      <div
        class="hidden max-lg:w-auto max-lg:h-full max-lg:ml-auto max-lg:flex max-lg:z-20"
      >
        <div
          class="m-auto m_button"
          id="m_button"
          :class="headerbtn ? 'active' : ''"
          @click="headerbtn = !headerbtn"
        >
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>

    <div
      ref="menu"
      class="fixed hidden overflow-hidden max-lg:z-10 max-lg:block max-lg:fixed max-lg:h-screen max-lg:w-screen max-lg:bg-black max-lg:top-0 max-lg:left-0"
      :style="{
        transform: 'translateY(-100%)',
        transition: 'transform 0.6s cubic-bezier(0.79, 0.21, 0.06, 0.81)',
      }"
      :class="headerbtn ? 'HeadDown' : ''"
    >
      <ul class="w-auto ml-5 mt-60">
        <animate-on-scroll animationClass="animate__fadeInUp" :once="false">
          <li
            class="inline mt-5 text-3xl font-bold text-white duration-100 hover:text-blue-700"
          >
            Team
          </li>
        </animate-on-scroll>

        <animate-on-scroll animationClass="animate__fadeInUp" :once="false">
          <li
            class="mt-5 text-3xl font-bold text-white duration-100 hover:text-blue-700"
          >
            Projects
          </li>
        </animate-on-scroll>
        <animate-on-scroll animationClass="animate__fadeInUp" :once="false">
          <li
            class="mt-5 text-3xl font-bold text-white duration-100 hover:text-blue-700"
          >
            Parts
          </li>
        </animate-on-scroll>
        <animate-on-scroll animationClass="animate__fadeInUp" :once="false">
          <li
            class="mt-5 text-3xl font-bold text-white duration-100 hover:text-blue-700"
          >
            Human Practices
          </li>
        </animate-on-scroll>
      </ul>
    </div>
  </div>
</template>




<script>
import AnimateOnScroll from "../components/AnimateOnScroll.vue";

export default {
  components: {
    AnimateOnScroll,
  },
  data() {
    return {
      lastScrollTop: 0,
      headerbtn: false,
      scrollThreshold: 100, // 控制滚动距离的阈值
      hideTimeout: 2000, // 设置用户停止滚动后的延迟时间，单位为毫秒
      scrollTimer: null, // 用于存储滚动定时器
    };
  },
  mounted() {
    // 节流函数
    const throttle = (func, limit) => {
      let lastFunc;
      let lastRan;
      return function (...args) {
        const context = this;
        if (!lastRan) {
          func.apply(context, args);
          lastRan = Date.now();
        } else {
          if (lastFunc) clearTimeout(lastFunc);
          lastFunc = setTimeout(function () {
            if (Date.now() - lastRan >= limit) {
              func.apply(context, args);
              lastRan = Date.now();
            }
          }, limit - (Date.now() - lastRan));
        }
      };
    };

    // 处理滚动事件的函数
    const handleScroll = () => {
      const navbar = this.$refs.headnav;
      const currentScrollTop =
        window.pageYOffset || document.documentElement.scrollTop;

      this.mouse = null;
      navbar.addEventListener("mouseenter", () => {
        this.mouse = false;
        // console.log(this.mouse);
      });
      navbar.addEventListener("mouseleave", () => {
        this.mouse = true;
        // console.log(this.mouse);
      });

      if (navbar && !this.headerbtn) {
        if (currentScrollTop > this.lastScrollTop + this.scrollThreshold) {
          // 向下滚动
          navbar.style.transform = "translateY(-150%)";
        } else if (
          currentScrollTop <
          this.lastScrollTop - this.scrollThreshold
        ) {
          // 向上滚动
          navbar.style.transform = "translateY(0)";
        }

        // 更新 lastScrollTop
        this.lastScrollTop = currentScrollTop <= 0 ? 0 : currentScrollTop;

        // 清除之前的定时器
        if (this.scrollTimer) clearTimeout(this.scrollTimer);

        // 设置新的定时器，在用户停止滚动后自动隐藏导航栏
        this.scrollTimer = setTimeout(() => {
          if (
            document.documentElement?.scrollTop != 0 &&
            !this.headerbtn &&
            this.mouse
          ) {
            navbar.style.transform = "translateY(-150%)";
          }
        }, this.hideTimeout);
        if (document.documentElement.scrollTop == 0) {
          navbar.style.transform = "translateY(0)";
        }
      }
    };

    // 初始化 scrollHandler
    this.scrollHandler = throttle(handleScroll.bind(this), 100);

    // 添加滚动事件监听器
    window.addEventListener("scroll", this.scrollHandler);
  },
  beforeDestroy() {
    // 移除滚动事件监听器
    window.removeEventListener("scroll", this.scrollHandler);
    // 清除定时器
    if (this.scrollTimer) clearTimeout(this.scrollTimer);
  },
};
</script>




<style scoped>
.m_button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  background-color: #eeeeee;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  box-shadow: -3px -3px 20px 0px #fff9, -1px -1px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9,
    inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9,
    inset 0px 0px 0px 0px #0001;
  transition: box-shadow 0.6s cubic-bezier(0.79, 0.21, 0.06, 0.81);
}

.m_button span {
  background-color: lightcoral;
  box-shadow: 0px 0px 10px 0px rgba(240, 128, 128, 0.3);
  width: 20px;
  height: 2px;
  border-radius: 2px;
  margin: 3px 0px 3px 0px;
  transition: margin 0.4s cubic-bezier(0.79, 0.21, 0.06, 0.81),
    transform 0.4s cubic-bezier(0.79, 0.21, 0.06, 0.81);
}

.m_button span:nth-child(2) {
  transform-origin: 50% 50%;
}

.active.m_button {
  box-shadow: 0px 0px 0px 0px #fff9, 0px 0px 0px 0px #fff9,
    0px 0px 0px 0px #0001, 0px 0px 0px 0px #0001, inset -7px -7px 20px 0px #fff9,
    inset -4px -4px 5px 0px #fff9, inset 7px 7px 20px 0px #0003,
    inset 4px 4px 5px 0px #0001;
}

.active span {
  margin: -1px;
}

.active span:nth-child(1) {
  transform: rotate(-45deg);
}

.active span:nth-child(2) {
  transform: scale(0);
}

.active span:nth-child(3) {
  transform: rotate(45deg);
}

.HeadDown {
  transform: translateY(0%) !important;
}

@media (hover: none) {
  li:hover{
    color: #fff;
  }
}
</style>