<script>
import lottie from "lottie-web";
import AnimateOnScroll from "../components/AnimateOnScroll.vue";
import MatterBg from "./components/MatterBg.vue";
import animationData from "@/assets/a1.json";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import AOS from "aos";
import "aos/dist/aos.css";
import SwiperMiddle from "./components/SwiperMiddle.vue";
import cubebg from "./components/cubebg.vue";
import GsapBox from "./components/GsapBox.vue";
import ThreeEarth from "./components/ThreeEarth.vue";
import MyLoading from "../components/MyLoading.vue";

gsap.registerPlugin(ScrollTrigger);
export default {
  data() {
    return {
      myloading: true,
      isMobile: window.innerWidth <= 1024,
      middle_key: 0,
      scroll_box_key: 0,
    };
  },
  components: {
    AnimateOnScroll,
    lottie,
    MatterBg,
    SwiperMiddle,
    cubebg,
    GsapBox,
    ThreeEarth,
    MyLoading,
  },
  methods: {
    initblackContainer() {
      gsap.set("#blackContainer", {
        scale: 0.9,
      });
      gsap.to("#blackContainer", {
        scale: 1,
        borderTopLeftRadius: "50px",
        borderTopRightRadius:"50px",
        scrollTrigger: {
          trigger: "#blackContainer",
          start: "top bottom",
          end: "top center",
          scrub: true,
          ease:"none"
        },
      });
    },
    initMyloading() {
      setTimeout(() => {
        this.myloading = false;
      }, 2000);
    },
    scrollbox() {
      this.scroll_box = gsap.context(() => {
        gsap.to("#scroll_box", {
          scrollTrigger: {
            trigger: "#matter_bg", // 触发的元素
            start: "top center", // 滚动开始时的位置
            end: "top top", // 滚动结束时的位置
            scrub: true, // 开启平滑滚动
            invalidateOnRefresh: true,
            // markers: true,
            onUpdate: (self) => {
              const progress = self.progress; // 获取滚动进度 (0 到 1)
              const scaleValue = gsap.utils.mapRange(0, 1, 1.0, 0.9, progress); // 将滚动进度映射为 1.0 到 0.9
              const borderRadius = gsap.utils.mapRange(0, 1, 0, 50, progress); // 将滚动进度映射为 16px 到 36px
              gsap.set("#scroll_box", {
                scale: scaleValue,
                borderRadius: `${borderRadius}px`, // 确保 px 单位
              });
            },
          },
        });
      });
    },
    loadAnimation() {
      lottie.loadAnimation({
        container: this.$refs.lottieContainer, // 指定动画容器
        renderer: "svg", // 渲染方式（svg, canvas, html）
        loop: true, // 是否循环播放
        autoplay: true, // 是否自动播放
        animationData,
      });
    },
    broundscroll() {
      const bround = this.$refs.headbround;
      gsap.set(bround, {
        width: "310px",
        height: "310px",
      });

      gsap.to(bround, {
        width: "1700px",
        height: "1700px",
        scrollTrigger: {
          trigger: this.$refs.headbround, // 触发动画的元素
          start: "top bottom", // 当元素顶部到达视口中心时开始动画
          end: "bottom center", // 当元素底部到达视口中心时结束动画
          scrub: true, // 滚动时动画进度会同步
        },
      });
    },
    svgscroll() {
      const path = this.$refs.svgPath;
      const pathLength = path.getTotalLength();

      // 设置初始状态
      gsap.set(path, {
        strokeDasharray: pathLength,
        strokeDashoffset: pathLength,
      });

      // 创建 ScrollTrigger 动画
      gsap.to(path, {
        strokeDashoffset: 0,
        ease: "none",
        scrollTrigger: {
          trigger: this.$refs.svgPath,
          start: "top center",
          end: "bottom top",
          scrub: 1,
          invalidateOnRefresh: true,
          // speed: 10, // 提高速度
        },
      });
    },
    handleResize() {
      // this.scroll_box?.kill();

      // this.scroll_box = null;
      // this.scrollbox();
      if (window.innerWidth <= 1024) {
        this.isMobile = true;
      } else {
        this.isMobile = false;
      }
      console.log("IsMobile", this.isMobile);
      // this.$bus.$emit("scrollbox", this.isMobile);
    },
  },
  watch: {
    isMobile() {
      this.$nextTick(() => {
        console.log("refersh了");
        ScrollTrigger.refresh();
      });
    },
  },
  mounted() {
    this.initMyloading();
    AOS.init();
    this.loadAnimation();
    this.svgscroll();
    this.broundscroll();
    this.scrollbox();
    this.handleResize();
    this.initblackContainer();
    window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>



<style scoped>
.arrow {
