<template>
  <div class="flex w-full min-h-screen bg-black rounded-b-[50px]">
    <table></table>
    <div class="relative m-auto" ref="container">
      <img
        src="@/assets/images/swiper4.png"
        alt=""
        class="max-h-[450px] brightness-75 rounded-2xl blur-[2px]"
        ref="HeaderWarp"
        style="transform-origin: center center -200px"
      />
      <div
        class="absolute -translate-x-1/3 bottom-1/2"
        ref="content"
        style="perspective: 500px; transform-origin: center center -200px"
      >
        <div class="z-10 font-extrabold tracking-wide text-white text-8xl">
          Project
        </div>
        <div class="flex mt-5 text-lg text-white">
          <div class="w-10 h-[2px] my-auto ml-auto bg-white mr-2"></div>
          Nefu-China
        </div>
        <div class="absolute h-1 translate-x-60 bg-white w-[700px] mt-5"></div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { gsap } from "gsap";

export default {
  methods: {
    getRotateDeg(range, value, length) {
      return (value / length) * (range[1] - range[0]) + range[0];
    },
    initProject() {
      const container = this.$refs.container;
      const content = this.$refs.content;
      const img = this.$refs.HeaderWarp;

      this.handleMouseMove = (e) => {
        const { offsetX, offsetY } = e;
        const { offsetWidth, offsetHeight } = container;

        const yRotation = this.getRotateDeg([-17, 17], offsetX, offsetWidth);
        const xRotation = -this.getRotateDeg([-17, 17], offsetY, offsetHeight);

        gsap.to([content, img], {
          duration: 2,
          rotationX: xRotation,
          rotationY: yRotation,
          transformPerspective: 500,
          ease: "expo.out",
        });
      };
      container.addEventListener("mousemove", this.handleMouseMove);
      this.handleMouseLeave = () => {
        gsap.to([content, img], {
          duration: 2,
          rotationX: 0,
          rotationY: 0,
          ease: "expo.out",
        });
      };
      container.addEventListener("mouseleave",this.handleMouseLeave);
    },
  },
  mounted() {
    this.initProject();
  },
  beforeDestroy() {
    const card = this.$refs.HeaderWarp;
    if (card) {
      card.removeEventListener("mousemove", this.handleMouseMove);
      card.removeEventListener("mouseleave", this.handleMouseLeave);
    }
  },
};
</script>
  
  <style>
/* 基础样式保持不变 */
</style>
  