<template>
  <div
    class="absolute overflow-hidden left-1/2 top-1/2 rounded-2xl min-h-[650px] cubewrapper"
    ref="cubewrapper"
    style="z-index: 0; transform: translate(-50%, -50%)"
  ></div>
</template>
  
  <script>
export default {
  props: {
    num: {
      type: Number,
      default: 1,
    },
  },
  mounted() {
    const wrapper = this.$refs.cubewrapper;
    let frag = document.createDocumentFragment();
    let str;
    if (this.num === 1) {
      str = `<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path fill="#fff" d="M8.025 22L6.25 20.225L14.475 12L6.25 3.775L8.025 2l10 10z"/></svg>`;
    } else if (this.num === 2) {
      str = `<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path fill="none" stroke="#fff" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 9h8V5.414a1 1 0 0 1 1.707-.707l6.586 6.586a1 1 0 0 1 0 1.414l-6.586 6.586A1 1 0 0 1 12 18.586V15H4a1 1 0 0 1-1-1v-4a1 1 0 0 1 1-1"/></svg>`;
    }
    for (let i = 0; i < 90; i++) {
      const cubeItem = document.createElement("div");
      cubeItem.className = "cubeitem";
      cubeItem.innerHTML = str;
      frag.appendChild(cubeItem);
    }

    wrapper.appendChild(frag);

    this.wrapper = wrapper; // 存储 wrapper，方便在 beforeDestroy 中访问

    wrapper.addEventListener("mousemove", (e) => {
      const sqrs = wrapper.querySelectorAll(".cubeitem");

      // 获取容器的边界框
      const wrapperRect = wrapper.getBoundingClientRect();

      // 获取鼠标相对于容器的坐标
      const mouseX = e.clientX - wrapperRect.left;
      const mouseY = e.clientY - wrapperRect.top;

      sqrs.forEach((sqr) => {
        const rect = sqr.getBoundingClientRect();
        const sqrX = rect.left - wrapperRect.left + rect.width / 2;
        const sqrY = rect.top - wrapperRect.top + rect.height / 2;

        const diffX = mouseX - sqrX;
        const diffY = mouseY - sqrY;

        const radians = Math.atan2(diffY, diffX);
        const angle = (radians * 180) / Math.PI;

        sqr.style.transform = `rotate(${angle}deg)`;
      });
    });
  },
  beforeDestroy() {
    if (this.wrapper) {
      this.wrapper.removeEventListener("mousemove", this.mousemoveHandler);
    }
  },
};
</script>
  
  <style scoped>
.cubewrapper {
  display: grid;
  grid-template-columns: repeat(9, 40px);
  grid-template-rows: repeat(7, 40px);
  grid-gap: 1.5rem;
  background-color: #ccbaff;
}
.cubewrapper .cubeitem {
  background-color: transparent;
}
.cubewrapper .cubeitem svg {
  display: block;
}
</style>
  