      // Events.on(mouseConstraint, "mouseup", function (event) {
      //   var mouseConstraint = event.source;
      //   var bodies = engine.world.bodies;
      //   if (!mouseConstraint.body) {
      //     for (var i = 0; i < bodies.length; i++) {
      //       var body = bodies[i];
      //       // Check if clicked or dragged
      //       console.log(click);
      //       if (click === true) {
      //         if (
      //           Matter.Bounds.contains(
      //             body.bounds,
      //             mouseConstraint.mouse.position
      //           )
      //         ) {
      //           var bodyUrl = body.url;
      //           console.log("Body.Url >> " + bodyUrl);
      //           // Hyperlinking feature
      //           if (bodyUrl != undefined) {
      //             window.open(bodyUrl, "_blank");
      //             console.log("Hyperlink was opened");
      //           }
      //           break;
      //         }
      //       }
      //     }
      //   }
      // });
      // 7. 执行渲染操作

      // 8. 创建运行方法
      this.engine = engine;
      let runner = Matter.Runner.create();
      this.runner = runner;
    },
    handleresize() {
      this.render.options.width = this.$refs.c?.parentElement.offsetWidth;
      this.render.options.height = this.$refs.c?.parentElement.offsetHeight;
    },
  },
  mounted() {
    this.initMatter();

    // 创建 IntersectionObserver 实例
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            console.log("begin");
            Matter.Render.run(this.render);
            Matter.Runner.run(this.runner, this.engine);
            // 一旦开始，就可以停止观察
            observer.unobserve(entry.target);
          }
        });
      },
      {
        root: null, // 使用视口作为观察根
        rootMargin: "0px",
        threshold: 0.1, // 当目标元素至少有 10% 在视口内时触发
      }
    );

    // 开始观察目标元素
    const target = this.$refs.c.querySelector("canvas");
    if (target) {
      observer.observe(target);
    }
  },
  beforeDestroy() {
    // 停止引擎和渲染器
    Matter.Render.stop(this.render);
    Matter.Runner.stop(this.runner);
    Matter.Engine.clear(this.engine);

    // 移除 mouseup 事件监听器
    const MouseConstraint = Matter.MouseConstraint;
    const Events = Matter.Events;

    // 获取已创建的鼠标约束
    const mouseConstraint = this.engine.world.constraints.find(
      (constraint) => constraint.label === "Mouse Constraint"
    );

    if (mouseConstraint) {
      // Events.off(mouseConstraint, "mouseup", this.mouseupHandler);
    }
    window.removeEventListener("resize",this.handleresize);
