</script>

<style scoped>
canvas {
  display: block; /* 移除默认的 canvas 可能带来的空隙 */
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: transparent; /* 确保背景透明 */
}
</style>